import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import React from "react";
import { BottomPayment, PaymentCard, TopNav } from "../../components";
import makeStyles from "@mui/styles/makeStyles";
import TotalPayment from "../../components/BottomPayment";
import { useNavigate } from "react-router-dom";
const useStyles = makeStyles(() => ({
  root: {
    // padding: "0 12px",
    paddingTop: "16px"
    // display: "flex",
    // justifyContent: "space-between",
    // alignItems: "center",
    // borderRadius: "6px",
  },
}));

const Payment = (props) => {
  const classes = useStyles();
  const navigate = useNavigate();
  return (
    <>
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title={"Buat Janji"}
            click={() => {
              navigate(-1);
            }}
          />
          <div>
            <Typography variant="subtitle2">
              Pembayaran yang dianjurkan
            </Typography>
            <PaymentCard selected={true} disabled={false} />
          </div>
          <div>
            <Typography variant="subtitle2">Pembayaran lainnya</Typography>
            <PaymentCard selected={true} disabled={false} />
          </div>
          <>
            <BottomPayment type={"total_payment"}/>
          </>
        </Stack>
      </Container>
    </>
  );
};

export default Payment;
